  //打开播放器出错界面
  updateListData = params => {
    let { vid, progress, progressTime } = params || {}
    let tempListData = JSON.parse(JSON.stringify(this.listData))
    tempListData.map(item => {
      let { video } = item || {}
      video.map(temp => {
        if (Number(temp.id || 0) === Number(vid || 0)) {
          temp.progress = progress
          temp.progress_time = progressTime
        }
      })
    })
    this.listData = tempListData
  }

