  /**获取课程目录*/
  getLessonChapters = async (id, unitid) => {
    this.id = id
    myGetData(`${api.newCourseChapters}`, { course_id: id })
      .then(({ status, data }) => {
        if (Number(status || 0) > 199 && Number(status || 0) < 300) {
          let { course, test } = data || {}
          course = course || []
          test = test || {}
          let tempAllUnit = []
          course.map((a, index) => {
            a.active = true
            let { sections, units } = a || {}
            sections = sections || []
            units = units || []
            sections.map(s => {
              let { units } = s || {}
              units.map(u => {
                tempAllUnit.push(u)
                u.active = false
              })
            })
            units.map(u => {
              tempAllUnit.push(u)
              u.active = false
            })
          })

          tempAllUnit.every(item => {
            if (Number(item.type || 0) === 1) {
              item.isFirstMovie = true
              return false
            } else {
              return true
            }
          })
          this.allUnits = tempAllUnit
          console.log(course)
          this.listData = course
          this.testData = test
          this.getUnitDetail(unitid)
        }
      })
      .catch(({ status, data }) => {})
  }

  //打开考试章节
  toggleSelTest = () => {
    this.sel_test = !this.sel_test
  }
