import React, { useEffect, useMemo } from 'react'
import { View } from '@tarojs/components'
import { useRouter, useShareAppMessage, useShareTimeline } from '@tarojs/taro'
import { useObserver } from 'mobx-react'
import TryVideo from '@/components/TryVideo'
import Taro from '@tarojs/taro'

import styles from './index.module.less'
import useStore from '@/util/useStore'
import EmptyList from '../../EmptyList'
import getHMS from '@/util/getHMS'
import getCurrentUrl from '@/util/getCurrentUrl'

const Study = props => {
  const router = useRouter()
  let { params } = router || {}
  let { cid, unitid } = params || {}
  cid = cid || null
  unitid = unitid || null

  const { store } = useStore()
  let { globalStore } = store || {}
  let { defaultBannerImage, defaultLogo, confirm_time, userData } = globalStore || {}

  userData = userData || {}
  let { id, name, info } = userData || {}
  let { idcard } = info || {}

  confirm_time = Number(confirm_time || 0)

  let {
    setUid,
    setConfirmTime,
    getLessonDetail,
    getLessonChapters,
    toggleUnit,
    toggleFollow,
    cleanAll,
  } = props
  setUid = setUid || new Function()
  setConfirmTime = setConfirmTime || new Function()
  getLessonDetail = getLessonDetail || new Function()
  getLessonChapters = getLessonChapters || new Function()
  toggleUnit = toggleUnit || new Function()
  toggleFollow = toggleFollow || new Function()
  cleanAll = cleanAll || new Function()

  let shareObj = {}

  const doWatchMovie = e => {
    e.stopPropagation()
    Taro.showToast({
      title: '您目前只能观看试看视频',
      icon: 'none',
      duration: 3000,
    })
  }

  useEffect(() => {
    setUid(id)
    setConfirmTime(confirm_time)
    getLessonDetail(cid)
    getLessonChapters(cid, unitid)
    return () => {
      cleanAll()
    }
  }, [])

  useShareAppMessage(res => {
    return shareObj
  })
  useShareTimeline(res => {
    return shareObj
  })

  return useObserver(() => {
    let {
      unitid,
      uid,
      info,
      listData,
      trailerTime,
      videoParams,
      videoDuration,
      testData,
      toggleSelTest,
      goToTest,
      sel_test,
      test_id,
    } = props
    testData = testData || {}
    toggleSelTest = toggleSelTest || new Function()
    goToTest = goToTest || new Function()
    sel_test = sel_test || false
    listData = listData || []
    trailerTime = Number(trailerTime || 0)
    let { title, image, content, source, is_follow } = info || {}

    image = image || defaultBannerImage
    content = content || {}
    content.content = content.content || ''
    content.content = content.content
      .replace(/<p([\s\w"=\/\.:;]+)((?:(style="[^"]+")))/gi, '<p')
      .replace(/<p([\s\w"=\/\.:;]+)((?:(class="[^"]+")))/gi, '<p')
      .replace(/<p>/gi, '<p style="textIndent:60px;marginBottom:20px">')
      .replace(/<img([\s\w"-=\/\.:;]+)((?:(height="[^"]+")))/gi, '<img$1')
      .replace(/<img([\s\w"-=\/\.:;]+)((?:(width="[^"]+")))/gi, '<img$1')
      .replace(/<img([\s\w"-=\/\.:;]+)((?:(style="[^"]+")))/gi, '<img$1')
      .replace(/<img([\s\w"-=\/\.:;]+)((?:(alt="[^"]+")))/gi, '<img$1')
      .replace(/<img/gi, '<img width="100%" height="auto" style="margin-bottom:10px;display:block"')

    source = source || {}
    source.avatar = source.avatar || defaultLogo
    is_follow = is_follow || false

    shareObj = {
      title,
      path: getCurrentUrl(),
      imageUrl: image,
    }
    const MemTryVideo = useMemo(
      () => (
        <TryVideo
          name={name}
          idcard={idcard}
          uid={uid}
          videoDuration={videoDuration}
          trailerTime={trailerTime}
          videoParams={videoParams}
        />
      ),
      [videoParams.src]
    )

    return (
      <View className={styles.page}>
        <View className={styles.video_content}>{MemTryVideo}</View>

        <View className={styles.info_content}>
          <View className={styles.info}>
            <View className={styles.name}>{title}</View>
          </View>

          <View className={styles.fav} onClick={toggleFollow}>
            <View className={[is_follow ? styles.signed : styles.sign].join(' ')} />
            <View className={styles.text}>收藏</View>
          </View>
        </View>
        <View className={styles.gray_con} />
        <View className={styles.tabs}>学习目录</View>
        {/*学习目录*/}
        <View className={styles.list}>
          {listData.length === 0 && Object.keys(testData).length === 0 ? <EmptyList /> : null}
          {listData.map((item, listIndex) => {
            let { id, title, sections, units, active } = item
            sections = sections || []
            units = units || []
            return (
              <View
                key={`chapter_${id}`}
                className={[styles.chapter, active ? styles.active : ''].join(' ')}
                onClick={e => {
                  e.stopPropagation()
                  toggleUnit(item)
                }}
              >
                <View className={styles.chapter_info}>
                  <View className={styles.chapter_sign} />
                  <View className={styles.chapter_tit}>{title}</View>
                </View>
                {sections.map(sectionItem => {
                  let { title, units } = sectionItem || {}
                  units = units || []
                  return (
                    <View key={`section_unit_${sectionItem.id}`}>
                      <View className={styles.section}>{title}</View>
                      {units.map(sectionUnit => {
                        let { type, typeName, title, total_time, total_question, isFirstMovie } =
                          sectionUnit || {}
                        type = Number(type || 0)
                        typeName = typeName || {}
                        return (
                          <View
                            className={[
                              styles.section_unit,
                              Number(sectionUnit.id || 0) === Number(unitid || 0)
                                ? styles.active
                                : '',
                            ].join(' ')}
                            key={`section_unit_${sectionUnit.id}`}
                            onClick={e => {
                              doWatchMovie(e)
                            }}
                          >
                            <View className={styles.section_unit_info_content}>
                              <View className={styles.section_unit_sign}>{typeName.name}</View>
                              <View className={styles.section_unit_info}>
                                <View className={styles.section_unit_info_left}>
                                  <View className={styles.section_unit_info_left_title}>
                                    <View className={styles.title}>{title}</View>
                                    {isFirstMovie ? (
                                      <View className={styles.title_sign}>试看</View>
                                    ) : null}
                                  </View>
                                  {type === 1 ? (
                                    <View className={styles.section_unit_info_left_info}>
                                      <View className={styles.unit_time} />
                                      <View className={styles.unit_time_str}>
                                        {getHMS(total_time)}
                                      </View>
                                    </View>
                                  ) : null}
                                  {type === 2 ? (
                                    <View className={styles.section_unit_info_left_info}>
                                      <View className={styles.unit_practise} />
                                      <View className={styles.unit_time_str}>
                                        {total_question}题
                                      </View>
                                    </View>
                                  ) : null}
                                </View>
                                {!isFirstMovie ? (
                                  <View className={styles.section_unit_info_right} />
                                ) : null}
                              </View>
                            </View>
                          </View>
                        )
                      })}
                    </View>
                  )
                })}

                {units.map(chapterUnit => {
                  let { type, typeName, title, total_time, total_question, isFirstMovie } =
                    chapterUnit || {}
                  type = Number(type || 0)
                  typeName = typeName || {}
                  return (
                    <View
                      key={`chapter_unit_${chapterUnit.id}`}
                      className={[
                        styles.section_unit,
                        Number(chapterUnit.id || 0) === Number(unitid || 0) ? styles.active : '',
                      ].join(' ')}
                      onClick={e => {
                        doWatchMovie(e)
                      }}
                    >
                      <View className={styles.section_unit_info_content}>
                        <View className={styles.section_unit_sign}>{typeName.name}</View>
                        <View className={styles.section_unit_info}>
                          <View className={styles.section_unit_info_left}>
                            <View className={styles.section_unit_info_left_title}>
                              <View className={styles.title}>{title}</View>
                              {isFirstMovie ? (
                                <View className={styles.title_sign}>试看</View>
                              ) : null}
                            </View>
                            {type === 1 ? (
                              <View className={styles.section_unit_info_left_info}>
                                <View className={styles.unit_time} />
                                <View className={styles.unit_time_str}>{getHMS(total_time)}</View>
                              </View>
                            ) : null}
                            {type === 2 ? (
                              <View className={styles.section_unit_info_left_info}>
                                <View className={styles.unit_practise} />
                                <View className={styles.unit_time_str}>{total_question}题</View>
                              </View>
                            ) : null}
                          </View>
                          {!isFirstMovie ? (
                            <View className={styles.section_unit_info_right} />
                          ) : null}
                        </View>
                      </View>
                    </View>
                  )
                })}
              </View>
            )
          })}

          {Object.keys(testData).length !== 0 ? (
            <View
              className={[styles.chapter, sel_test ? styles.active : ''].join(' ')}
              onClick={e => {
                e.stopPropagation()
                toggleSelTest()
              }}
            >
              <View className={styles.chapter_info}>
                <View className={styles.chapter_sign} />
                <View className={styles.chapter_tit}>课后考试</View>
              </View>

              <View
                className={[styles.section_unit, test_id ? styles.active : ''].join(' ')}
                onClick={e => {
                  e.stopPropagation()
                  goToTest()
                }}
              >
                <View className={styles.section_unit_info_content}>
                  <View className={styles.section_unit_sign}>考试</View>
                  <View className={styles.section_unit_info}>
                    <View className={styles.section_unit_info_left}>
                      <View className={styles.section_unit_info_left_title}>
                        <View
                          className={[
                            styles.title,
                            Number(test_id || 0) !== 0 ? styles.active : '',
                          ].join(' ')}
                        >
                          {testData.title}
                        </View>
                      </View>
                    </View>
                  </View>
                </View>
              </View>
            </View>
          ) : null}
        </View>
      </View>
    )
  })
}

export default Study
